
    import ENVIRONMENT_CONFIG from './config/envConfig.js'
    import PAGE_CONFIG from './config/pageConfig.js'
	import Utils from "./utils/util.js"
	import { mapState } from 'vuex'
	export default {
        globalData: {
            ENVIRONMENT_CONFIG,
            PAGE_CONFIG
		},
		computed: {
			...mapState(['hasLogin'])
		},
        onLaunch: function() {
			// 登录
			console.log('App Launch');
			uni.getStorage({
				key: 'userInfoRelation',
				success: res => {
					if(res.data.token){
						this.$store.commit('setAuthorization', true);
						this.$store.commit('setRegister', true);
						this.$store.commit('setToken', res.data.token);
					}
				},
				fail: () => {
					console.log('需要重新授权')
				}
			})
			uni.getStorage({
				key: 'unionIdRelation',
				success: res => {
					if(res.data.unionId){
						this.$store.commit('setUnionId', res.data.unionId);
					}
				}
			})
		},
		onShow: function() {
			console.log('App Show');
		},
		onHide: function() {
			console.log('App Hide');
		}
	}
